@import './reset.css';
@import './slider.css';

.inner {
  width: 80%;
  margin: 0 auto;
}

header {
  width: 100%;
  height: 150px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  transition: 0.3s;
}

.header_menu_container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

header .header_menu_container .responsive_menu_icon_container {
  display: none;
}

header .header_menu_container .header_menu_container_left {
  display: flex;
  justify-content: center;
}

header .header_menu_container .header_menu_container_left .menu_icon_container {
  width: 10%;
  text-align: center;
}

header .header_menu_container .header_menu_container_left .menu_icon_container .menu_icon {
  font-size: 22px;
  color: #fff;
}

header .header_menu_container .header_menu_container_left .menu_wrapper {
  display: flex;
  text-align: center;
}

header .header_menu_container .header_menu_container_left .menu_wrapper .menu_list {
  width: 150px;
  position: relative;
}

header .header_menu_container .header_menu_container_left .menu_wrapper .menu_list>a {
  font-size: 22px;
  color: #ac9272;
}

header .header_menu_container .header_menu_container_left .menu_wrapper .menu_list>a:hover {
  color: #fff;
  transition: all 0.5s;
}

header .header_menu_container .header_menu_container_left .menu_wrapper .menu_list .menu_smenu {
  position: absolute;
  top: 50px;
  left: 0;
  background-color: #fff;
  display: none;
}

header .header_menu_container .header_menu_container_left .menu_wrapper .menu_list .menu_smenu .smenu_list {
  width: 150px;
  height: 35px;
  font-size: 15px;
  padding: 5% 10%;
  cursor: pointer;
}

header .header_menu_container .header_menu_container_left .menu_wrapper .menu_list .menu_smenu .smenu_list:hover {
  color: #fff;
  background-color: #f15912;
  transition: all 0.5s;
}

header .header_menu_container .logo {
  display: flex;
  justify-content: center;
}


header .header_menu_container .logo a img {
  width: 100%;
  display: block;
}

header .header_menu_container .header_menu_container_right {
  display: flex;
  justify-content: center;
}

