.aside_show {
  transform: translateX(-381px);
}

#aside_content {
  position: fixed;
  top: 0;
  left: 0;
  width: 345px;
  height: 100vh;
  background-color: #F6F6F6;
  overflow-y: scroll;
  z-index: 99999;
  transition: 0.6s;
}

#aside_content #aside_close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
}

#aside_content .aside_container {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#aside_content .aside_container>img {
  width: 60%;
}

#aside_content .aside_container>p {
  font-family: "Josefin Sans", sans-serif;
  font-weight: bold;
  margin: 20px 0;
}

#aside_search {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  cursor: url(data:image/png;base64,), progress;
  display: none;
}

#aside_search::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: #000;
  opacity: 0.8;
  z-index: -1;
  border-radius: 0px 0 0px 400px;
  transition: 0.3s;
}

.form_container {
  display: none;
}

.form_container form {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999999;
  display: flex;
  justify-content: center;
}

.form_container form input {
  width: 400px;
  border: none;
  height: 54px;
  padding: 0 90px 0 30px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: #fff;
}

.form_container form input:focus {
  outline: none;
}

.form_container form button {
  height: 54px;
  background-color: #fd7214;
  width: 70px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.responsive_menu_container {
  position: fixed;
  top: 0;
  right: 0;
  width: 300px;
  height: 100vh;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 0 0 0;
  z-index: 9999999;
  display: none;
}

.responsive_menu_container .responsive_row {
