  display: block;
  font-size: 16px;
  border-bottom: 1px solid #e5e5e5;
}

.responsive_menu_container .reponsive_nav .responsive_menu_wrapper .responsive_menu_list .responsive_menu_smenu .responsive_smenu_list:last-child a {
